.contact-form {
  position: relative;
  min-height: 840px;
  background: linear-gradient(90deg, #026593 0 50%, #00b2ff 50% 100%);
  overflow: hidden;
}
@media (max-width: 1023px) {
  .contact-form {
    padding: 50px 0 125px;
    min-height: auto;
    background: #00b2ff;
  }
}
@media (max-width: 767px) {
  .contact-form {
    padding: 50px 0 90px;
  }
}
.contact-form::before {
  content: "";
  position: absolute;
  top: 0;
  right: 50%;
  transform: translateX(50%);
  width: 1920px;
  height: 100%;
  background: url("../../img/svg/plane.svg") 350px 246px/510px 285px no-repeat, linear-gradient(139deg, #026593 0 633px, #00b2ff 633px 100%);
}
@media (max-width: 1319px) {
  .contact-form::before {
    right: -300px;
    transform: translateX(0);
  }
}
@media (max-width: 1023px) {
  .contact-form::before {
    display: none;
  }
}

.contact-form__container {
  position: relative;
  z-index: 1;
  margin: 160px 0 0 auto;
  padding: 10px;
  width: 580px;
  min-height: 420px;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .contact-form__container {
    margin: 0;
    width: 100%;
  }
}

.form__title {
  margin: 0 0 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
}

.form__description {
  margin: 0 0 40px;
  font-size: 14px;
  line-height: 19px;
}

.form__grid {
  display: grid;
  row-gap: 20px;
}

.form__item {
  position: relative;
}
.form__item.is-invalid {
  outline: 1px solid #ffff00;
  border-radius: 3px;
}

.form__item .input-message {
  position: absolute;
  top: 100%;
  z-index: 50;
  width: 100%;
  font-size: 13px;
  line-height: 20px;
  padding: 0 2px 0;
  color: #ffff00;
}

.form__item--checkbox {
  justify-self: center;
  align-self: center;
}

.form input:not([type=checkbox]),
.form textarea {
  display: block;
  width: 100%;
  padding: 15px;
  background-color: rgba(4, 24, 39, 0.15);
  color: inherit;
  border: none;
  border-radius: 3px;
}
.form input:not([type=checkbox])::placeholder,
.form textarea::placeholder {
  color: inherit;
}
@media (hover: hover) {
  .form input:not([type=checkbox]):hover:not(.focus-visible),
.form textarea:hover:not(.focus-visible) {
    background-color: rgba(4, 24, 39, 0.3);
  }
}
.form input:not([type=checkbox]).focus-visible:focus,
.form textarea.focus-visible:focus {
  color: #041827;
  background-color: #ffffff;
}
.form input:not([type=checkbox]).focus-visible:focus:placeholder-shown,
.form textarea.focus-visible:focus:placeholder-shown {
  color: inherit;
  background-color: rgba(4, 24, 39, 0.6);
}

.form textarea {
  height: 155px;
  margin-bottom: 10px;
}

.form button {
  display: block;
  padding: 0 25px;
  min-width: 265px;
  width: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  line-height: 45px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: none;
  border-radius: 3px;
  color: inherit;
  background-color: #041827;
  cursor: pointer;
}
@media (hover: hover) {
  .form button:hover:not(.focus-visible) {
    background-color: rgba(4, 24, 39, 0.8);
  }
  .form button:hover:not(.focus-visible):active {
    background-color: rgba(4, 24, 39, 0.6);
  }
}
.form button.focus-visible:focus {
  background-color: rgba(4, 24, 39, 0.8);
}
.form button.focus-visible:focus:active {
  background-color: rgba(4, 24, 39, 0.6);
}

/* 2 columns version */
@media (min-width: 768px) {
  .form--static .form__title {
    font-size: 36px;
    line-height: 51px;
  }
  .form--static .form__description {
    margin: 0 0 35px;
    font-size: 16px;
    line-height: 22px;
  }
  .form--static .form__grid {
    grid-template-columns: 1fr 1fr;
    row-gap: 20px;
    column-gap: 30px;
  }
  .form--static .form__item--textarea {
    grid-column: 1/3;
  }
  .form--static .form__item--checkbox {
    margin: 0 0 0 10px;
  }
  .form--static textarea {
    margin-bottom: 15px;
  }
  .form--static button {
    width: auto;
  }
}

.checkbox {
  position: relative;
  display: block;
}

.checkbox label {
  display: block;
  padding: 10px 0 10px 25px;
}
@media (hover: hover) {
  .checkbox label:hover:not(.focus-visible) {
    cursor: pointer;
  }
  .checkbox label:hover:not(.focus-visible) .checkbox__mark {
    background-color: rgba(4, 24, 39, 0.3);
  }
}

.checkbox__mark {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}

.checkbox input:checked ~ .checkbox__mark::before {
  content: "";
  position: absolute;
  top: 20%;
  left: 32%;
  transform: rotate(45deg);
  width: 4px;
  height: 6px;
  border-bottom: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
}

.checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.checkbox input.focus-visible:focus ~ .checkbox__mark {
  outline: 3px solid #041827;
}

.checkbox input + span {
  display: block;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.8;
}

