.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($black-pearl-2, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  @include vp-1023 {
    display: none;
  }

  &::-webkit-scrollbar {
    display: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}

.modal__wrapper {
  position: relative;

  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 40px 40px;

  @include vp-767 {
    padding: 60px 16px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 500px;
  padding: 40px;
  background-color: $deep-sky-blue;
  color: $color-default-white;
}

.modal__close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;

  width: 40px;
  height: 40px;
  padding: 0;

  background: $color-transparent;
  border: none;
  cursor: pointer;
  color: inherit;

  @include hover-focus {
    opacity: 0.6;

    &:active {
      opacity: 0.3;
    }
  }
}
